import React from "react";
import { Link, NavLink } from "react-router-dom";
import Row from "muicss/lib/react/row";
import getParameterByName from "../common/getParameterByName";

export default class MyLoansDealDetailsContainer extends React.Component {
  render() {
    let in_nyc = getParameterByName("in_nyc");
    let violations = getParameterByName("violations");
    let additionalProperties = getParameterByName("additional_properties");
    let dealStatus = getParameterByName("deal_status");
    return (
      <div>
        <Row className="sub-header">
          <Link
            className="back-arrow"
            to={getParameterByName("previous_page") || `/loans/my-loans`}
          >
            <img src="/images/icons/arrow-left.png" alt="arrow-left" />
          </Link>
          <ul className="deal-details-nav">
            {additionalProperties > 0 ? (
              <li className="deal-details-nav-item">
                <NavLink
                  to={{
                    pathname: `/my-loans-dd/property-details`,
                    search: window.location.search,
                  }}
                  activeClassName="active-deal-nav-item"
                >
                  Property Details
                </NavLink>
              </li>
            ) : (
              ""
            )}
            <li className="deal-details-nav-item">
              <NavLink
                to={{
                  pathname: `/my-loans-dd/write-up`,
                  search: window.location.search,
                }}
                activeClassName="active-deal-nav-item"
              >
                Write Up
              </NavLink>
            </li>
            {dealStatus === "2" ? (
              <li className="deal-details-nav-item">
                <NavLink
                  to={{
                    pathname: `/my-loans-dd/status-summary`,
                    search: window.location.search,
                  }}
                  activeClassName="active-deal-nav-item"
                >
                  Status Summary
                </NavLink>
              </li>
            ) : (
              ""
            )}
            <li className="deal-details-nav-item">
              <NavLink
                to={{
                  pathname: `/my-loans-dd/deal-contacts`,
                  search: window.location.search,
                }}
                activeClassName="active-deal-nav-item"
              >
                Deal Contacts
              </NavLink>
            </li>
            <li className="deal-details-nav-item">
              <NavLink
                to={{
                  pathname: `/my-loans-dd/personal-docs`,
                  search: window.location.search,
                }}
                activeClassName="active-deal-nav-item"
              >
                Property Docs
              </NavLink>
            </li>
            {in_nyc === "true" && violations >= 0 ? (
              <li className="deal-details-nav-item">
                <NavLink
                  to={{
                    pathname: `/my-loans-dd/violations`,
                    search: window.location.search,
                  }}
                  activeClassName="active-deal-nav-item"
                >
                  Violations
                </NavLink>
              </li>
            ) : (
              ""
            )}
          </ul>
        </Row>
        <div className="deal-padding-top">{this.props.children}</div>
      </div>
    );
  }
}
