import React /*, { useState } */ from "react";
import {
  init as Sentry_init,
  configureScope as Sentry_configureScope,
  withScope as Sentry_withScope,
  captureException as Sentry_captureException,
  showReportDialog as Sentry_showReportDialog,
  captureMessage as Sentry_captureMessage,
} from "@sentry/browser";
import packagejson from "../../../package.json";
import { Button } from "@material-ui/core";
import ErrorPage from "./ErrorPage";
// import { useStyles } from "./ErrorPage.style";
// import { clearEcalc } from "../../actions/eCalcForms"; //"../../../actions/eCalcForms";
// import Row from "muicss/lib/react/row";
// import ClearConfirmationModal from "./ClearConfirmationModal";
// import { Navigation } from "../../lib";

const { NODE_ENV = "development" } = process.env;

const __DEV__ = NODE_ENV === "development";

/** See https://docs.sentry.io/learn/configuration */
Sentry_init({
  /** Do not send errors in development. */
  beforeSend: __DEV__
    ? function () {
        return null;
      }
    : undefined,
  /** Where to send errors. This is NOT a secret. */
  dsn: "https://c5ac47fc8403425f9302d453f816d36b@sentry.io/1851372",
  environment: NODE_ENV,
  release: packagejson.version,
});

export class ErrorSentry extends React.Component {
  static setUser(user) {
    Sentry_configureScope((scope) => {
      scope.setUser({
        id: user.id,
        username: `${user.name_first} ${user.name_last}`,
      });
    });
  }

  static captureMessage(msg, level = "error") {
    Sentry_captureMessage(msg, level);
  }

  constructor(props) {
    super(props);
    this.state = { error: null };
  }

  componentDidCatch(error, errorInfo) {
    this.setState({ error });
    Sentry_withScope((scope) => {
      Object.keys(errorInfo).forEach((key) => {
        scope.setExtra(key, errorInfo[key]);
      });
      Sentry_captureException(error);
    });
  }

  render() {
    if (this.state.error) {
      //render fallback UI
      console.log("ssss", this.props.showClearData);
      return (
        <ErrorPage showClearData={this.props.showClearData}>
          <a href="/" onClick={this.showReportDialog}>
            <Button
              style={{
                color: "#f5f5f5",
                backgroundColor: "#24d7d1",
                padding: "8px 15px",
                height: "auto",
                fontSize: 14,
                fontWeight: "bold",
                margin: "auto 10px",
              }}
            >
              Report feedback
            </Button>
          </a>
        </ErrorPage>
      );
    } else {
      //when there's not an error, render children untouched
      return this.props.children;
    }
  }

  showReportDialog = (e) => {
    e.preventDefault();
    Sentry_showReportDialog();
  };
}

