import React from "react";
import { connect } from "react-redux";
import * as interestOnlyActions from "../../../actions/calculatorActions/interestOnlyActions";
import * as sharingCalculationsActions from "../../../actions/calculatorActions/sharingCalculationsActions";
import DollarDisplay from "../calculatorCommon/DollarDisplay";
import DollarInput from "../calculatorCommon/DollarInput";
import PercentInput from "../calculatorCommon/PercentInput";
import Row from "muicss/lib/react/row";
import Panel from "muicss/lib/react/panel";
import Form from "muicss/lib/react/form";
import * as centralStateActions from "../../../actions/calculatorActions/centralActions";
import formatValue from "../calculatorCommon/FormatValueMethod";
import formatDisplay from "../calculatorCommon/FormatDisplayMethod";
import ShareCalcModal from "../calculatorCommon/shareCalculationsModal";

class InterestOnly extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      totalInterestAnnually: this.props.totalInterestAnnually,
      interestPerMonth: this.props.interestPerMonth,
      loanAmount: this.props.loanAmount,
      interestRate: this.props.interestRate,

      modalOpen: false,
      from: "",
      to: "",
      cc: "",
    };

    this.update = this.update.bind(this);
    this.updateCentralState = this.updateCentralState.bind(this);
  }

  componentWillUnmount() {
    this.props.dispatch(interestOnlyActions.updateOnUnmount(this.state));
  }

  update(name, value) {
    let stateObj = {};
    stateObj[name] = value;
    this.setState(stateObj, function () {
      if (this.state.loanAmount && this.state.interestRate) {
        this.props.dispatch(
          interestOnlyActions.calculateInterestOnly(this.state)
        );
      } else {
        this.props.dispatch(
          interestOnlyActions.updateValuesInterestOnly(this.state)
        );
      }
    });
  }

  updateCentralState(name, value) {
    let stateObj = {};
    stateObj[name] = value;
    this.setState(stateObj, function () {
      this.props.dispatch(centralStateActions.updateCentralValues(name, value));
      if (this.state.loanAmount && this.state.interestRate) {
        this.props.dispatch(
          interestOnlyActions.calculateInterestOnly(this.state)
        );
      } else {
        this.props.dispatch(
          interestOnlyActions.updateValuesInterestOnly(this.state)
        );
      }
    });
  }

  componentDidMount() {
    this.update("loanAmount", this.props.loanAmount);
  }

  openSharingCalculatorModal() {
    this.setState({ modalOpen: true });
  }

  closeModal() {
    this.setState({ modalOpen: false });
  }

  afterOpenModal() {}

  updateFrom(value) {
    this.setState({ from: value });
  }

  updateTo(value) {
    this.setState({ to: value });
  }

  updateCC(value) {
    this.setState({ cc: value });
  }

  onSubmit(e) {
    e.preventDefault();
    this.setState({ modalOpen: false }, function () {
      var input = [
        { input0: formatValue(this.state.loanAmount).toString() },
        { input1: (this.state.interestRate + "%").toString() },
      ];

      var output = [
        { output0: formatDisplay(this.state.totalInterestAnnually).toString() },
        { output1: formatDisplay(this.state.interestPerMonth).toString() },
      ];

      this.props.dispatch(
        sharingCalculationsActions.calculatorSharing(
          this.state.to,
          this.state.from,
          this.state.cc,
          "InterestOnly",
          input,
          output
        )
      );
    });
  }

  render() {
    return (
      <div>
        <Row>
          <ShareCalcModal
            isOpen={this.state.modalOpen}
            onRequestClose={this.closeModal.bind(this)}
            onAfterOpen={this.afterOpenModal.bind(this)}
            onSubmit={this.onSubmit.bind(this)}
            from={this.state.from}
            to={this.state.to}
            cc={this.state.cc}
            updateFrom={this.updateFrom.bind(this)}
            updateTo={this.updateTo.bind(this)}
            updateCC={this.updateCC.bind(this)}
          />
          <Panel className="calculator-card">
            <Form className="calculator-form" inline={true}>
              <h2 className="calculator-title">Interest Only</h2>
              <div className="margin-top"></div>
              <DollarInput
                name="loanAmount"
                value={this.state.loanAmount}
                update={this.updateCentralState}
                label="Loan Amount"
              />
              <br />
              <br />
              <PercentInput
                name="interestRate"
                value={this.state.interestRate}
                update={this.updateCentralState}
                label="Rate Per Year"
              />
            </Form>
            <div id="boxes">
              <p
                className={
                  this.state.loanAmount && this.state.interestRate
                    ? "share-calc"
                    : "hidden"
                }
                onClick={this.openSharingCalculatorModal.bind(this)}
              >
                <i className="material-icons">share</i>Share Calculations
              </p>
              <div className="box">
                <p className="box-title">Total Interest Annually</p>
                <p className="amount">
                  <DollarDisplay
                    name="totalInterestAnnually"
                    value={this.state.totalInterestAnnually}
                  />
                </p>
              </div>
              <div className="box">
                <p className="box-title">Interest Per Month</p>
                <p className="amount">
                  <DollarDisplay
                    name="interestPerMonth"
                    value={this.state.interestPerMonth}
                  />
                </p>
              </div>
            </div>
          </Panel>
        </Row>

        <div className="info">
          <p>
            <i className="material-icons">email</i>For a detailed discussion of
            these numbers or to discuss any of your commercial real estate
            financing needs, please contact an {/* GPARENCY */} broker by clicking{" "}
            <a href="mailto:connect@gparency.com">here</a>
          </p>
          <p>
            <i className="material-icons">info</i>Calculator results shall not
            be relied on for making financial decisions. Actual calculators may
            rely on estimates and rounding. Before any final decisions are made,
            an expert should be consulted.
          </p>{" "}
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const state1 = state.interestOnly;
  const state2 = state.centralValues;
  return {
    totalInterestAnnually: state1.totalInterestAnnually || "",
    interestPerMonth: state1.interestPerMonth || "",
    loanAmount: state2.loanAmount || "",
    interestRate: state2.interestRate || "",
  };
}

export default connect(mapStateToProps)(InterestOnly);
