import React from "react";
import { connect } from "react-redux";
import * as reverseMortgageActions from "../../../actions/calculatorActions/reverseMortgageActions";
import * as sharingCalculationsActions from "../../../actions/calculatorActions/sharingCalculationsActions";
import * as centralActions from "../../../actions/calculatorActions/centralActions";
import DollarInput from "../calculatorCommon/DollarInput";
import PercentInput from "../calculatorCommon/PercentInput";
import NumberInput from "../calculatorCommon/NumberInput";
import DollarDisplay from "../calculatorCommon/DollarDisplay";
import Row from "muicss/lib/react/row";
import Panel from "muicss/lib/react/panel";
import Form from "muicss/lib/react/form";
import formatValue from "../calculatorCommon/FormatValueMethod";
import formatDisplay from "../calculatorCommon/FormatDisplayMethod";
import ShareCalcModal from "../calculatorCommon/shareCalculationsModal";

class ReverseMortgage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loanAmount: props.loanAmount,
      monthlyPayment: props.monthlyPayment,
      interestRate: props.interestRate,
      amortizationYears: props.amortizationYears,
      amortizationMonths: props.amortizationMonths,

      modalOpen: false,
      from: "",
      to: "",
      cc: "",
    };

    this.update = this.update.bind(this);
    this.updateCentralState = this.updateCentralState.bind(this);
  }

  update(name, value) {
    let stateObj = {};
    stateObj[name] = value;
    this.setState(stateObj, function () {
      this.props.dispatch(
        reverseMortgageActions.calculateReverseMortgage(this.state, {
          name,
          value,
        })
      );
    });
  }

  updateCentralState(name, value) {
    let stateObj = {};
    stateObj[name] = value;
    if (name === "amortizationYears") {
      const monthsName = "amortizationMonths";
      const monthsValue = value * 12;
      let stateObj2 = {};
      stateObj2[monthsName] = monthsValue;
      this.setState(stateObj, function () {
        this.props.dispatch(
          centralActions.updateCentralValues(monthsName, monthsValue)
        );
      });
    } else if (name === "amortizationMonths") {
      const yearsName = "amortizationYears";
      const yearsValue = value / 12;
      let stateObj2 = {};
      stateObj2[yearsName] = yearsValue;
      this.setState(stateObj, function () {
        this.props.dispatch(
          centralActions.updateCentralValues(yearsName, yearsValue)
        );
      });
    }

    this.setState(stateObj, function () {
      this.props.dispatch(centralActions.updateCentralValues(name, value));
      this.props.dispatch(
        reverseMortgageActions.calculateReverseMortgage(this.state, {
          name,
          value,
        })
      );
    });
  }

  componentDidMount() {
    this.update("interestRate", this.props.interestRate);
  }

  openSharingCalculatorModal() {
    this.setState({ modalOpen: true });
  }

  closeModal() {
    this.setState({ modalOpen: false });
  }

  updateFrom(value) {
    this.setState({ from: value });
  }

  updateTo(value) {
    this.setState({ to: value });
  }

  updateCC(value) {
    this.setState({ cc: value });
  }

  onSubmit(e) {
    e.preventDefault();
    this.setState({ modalOpen: false }, function () {
      var input = [
        { input0: formatValue(this.state.monthlyPayment).toString() },
        { input1: (this.state.interestRate + "%").toString() },
        { input2: this.state.amortizationYears.toString() },
        { input3: this.state.amortizationMonths.toString() },
      ];

      var output = [
        { output0: formatDisplay(this.state.loanAmount).toString() },
      ];

      this.props.dispatch(
        sharingCalculationsActions.calculatorSharing(
          this.state.to,
          this.state.from,
          this.state.cc,
          "ReverseMortgage",
          input,
          output
        )
      );
    });
  }

  render() {
    return (
      <div>
        <Row>
          <ShareCalcModal
            isOpen={this.state.modalOpen}
            onRequestClose={this.closeModal.bind(this)}
            onAfterOpen={() => {}}
            onSubmit={this.onSubmit.bind(this)}
            from={this.state.from}
            to={this.state.to}
            cc={this.state.cc}
            updateFrom={this.updateFrom.bind(this)}
            updateTo={this.updateTo.bind(this)}
            updateCC={this.updateCC.bind(this)}
          />
          <Panel className="calculator-card">
            <Form className="calculator-form" inline={true}>
              <h2 className="calculator-title">Calculate Original Balance</h2>
              <div className="margin-top"></div>
              <DollarInput
                name="monthlyPayment"
                value={this.state.monthlyPayment}
                update={this.update}
                label="Monthly Payment"
              />
              <br />
              <br />
              <PercentInput
                name="interestRate"
                value={this.state.interestRate}
                update={this.updateCentralState}
                label="Rate"
              />
              <br />
              <br />
              <NumberInput
                name="amortizationYears"
                value={this.state.amortizationYears}
                update={this.updateCentralState}
                label="Amortization Years"
              />
              <NumberInput
                name="amortizationMonths"
                value={this.state.amortizationMonths}
                update={this.updateCentralState}
                label="Amortization Months"
              />
              <br />
              <br />
            </Form>
            <div id="boxes">
              <p
                className={
                  this.state.loanAmount &&
                  this.state.amortizationYears &&
                  this.state.interestRate &&
                  this.state.monthlyPayment
                    ? "share-calc"
                    : "hidden"
                }
                onClick={this.openSharingCalculatorModal.bind(this)}
              >
                <i className="material-icons">share</i>Share Calculations
              </p>
              <div className="box">
                <p className="box-title">Original Principal Balance</p>
                <p className="amount">
                  <DollarDisplay
                    name="loanAmount"
                    value={this.props.loanAmount}
                  />
                </p>
              </div>
            </div>
          </Panel>
        </Row>

        <div className="info">
          <p>
            <i className="material-icons">email</i>For a detailed discussion of
            these numbers or to discuss any of your commercial real estate
            financing needs, please contact an {/* GPARENCY */} broker by clicking{" "}
            <a href="mailto:connect@gparency.com">here</a>
          </p>
          <p>
            <i className="material-icons">info</i>Calculator results shall not
            be relied on for making financial decisions. Actual calculators may
            rely on estimates and rounding. Before any final decisions are made,
            an expert should be consulted.
          </p>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const reverseMortgageValues = state.reverseMortgage;
  const centralValues = state.centralValues;
  return {
    loanAmount: reverseMortgageValues.loanAmount,
    monthlyPayment: reverseMortgageValues.monthlyPayment,
    interestRate: centralValues.interestRate,
    amortizationYears: centralValues.amortizationYears,
    amortizationMonths: centralValues.amortizationMonths,
  };
}

export default connect(mapStateToProps)(ReverseMortgage);
