import React from "react";
import Input from "muicss/lib/react/input";
import ErrorMessage from "./ErrorMessage";

class FormattedInput extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      actualValue: "",
      formattedValue: "",
      errorMessage: "",
    };

    this.validate = this.validate.bind(this);
  }

  componentDidMount() {
    let formattedValue = this.props.value
      ? this.props.formatValue(this.props.value)
      : "";
    this.setState({ formattedValue, actualValue: this.props.value });
    this.validate(
      this.props.value,
      this.props.minValue,
      this.props.maxValue,
      this.props.required
    );
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (
      this.props.value !== nextProps.value ||
      this.props.minValue !== nextProps.minValue ||
      this.props.maxValue !== nextProps.maxValue
    ) {
      let formattedValue = nextProps.value
        ? this.props.formatValue(nextProps.value)
        : "";
      this.setState({ formattedValue, actualValue: this.props.value });
      this.validate(
        nextProps.value,
        nextProps.minValue,
        nextProps.maxValue,
        nextProps.required
      );
    }
  }

  onChange(e) {
    let element = e.target;
    let value = e.target.value;
    let actualValue = this.props.stripFormat(value);
    let formattedValue = "";
    if (actualValue && (!!this.props.allowZero || actualValue !== "0")) {
      if (parseFloat(actualValue) > Number.MAX_SAFE_INTEGER) {
        actualValue = actualValue.slice(0, -1);
      } else if (actualValue === ".") {
        actualValue = "0.";
      }
      formattedValue = this.props.formatValue(actualValue);
    } else {
      actualValue = "";
      formattedValue = "";
    }
    let endPosition = element.value.length - element.selectionStart || 0;
    this.setState(
      {
        actualValue,
        formattedValue,
      },
      () => {
        this.props.update(this.props.name, this.state.actualValue);
        if (formattedValue.slice(-1) === "%") {
          setTimeout(() => {
            element.focus();
            let position = this.state.formattedValue.length - endPosition;
            if (endPosition === 0) {
              position = position - 1;
            }
            element.setSelectionRange(position, position);
          }, 0);
        }
      }
    );
  }

  validate(inputValue, minValue, maxValue, required) {
    minValue = parseFloat(minValue);
    maxValue = parseFloat(maxValue);
    inputValue = parseFloat(inputValue);

    if (required && !inputValue) {
      this.setState({ errorMessage: "this field is required" });
    } else if (minValue && inputValue < minValue) {
      this.setState({
        errorMessage:
          "Value must be greater than " + this.props.formatValue(minValue),
      });
    } else if (maxValue && inputValue > maxValue) {
      this.setState({
        errorMessage:
          "Value must be less than " + this.props.formatValue(maxValue),
      });
    } else {
      this.setState({ errorMessage: "" });
    }
  }

  onBlur() {
    if (this.props.onBlur && this.state.actualValue) {
      this.props.onBlur(this.state.actualValue);
    }
  }

  render() {
    const customClass = this.props.customClass;
    return (
      <div
        className={`formatted-input-container ${
          customClass ? customClass : ""
        }`}
      >
        <Input
          type="text"
          noValidate={true}
          inputMode="decimal"
          label={this.props.label}
          floatingLabel={true}
          name={this.props.name}
          value={this.state.formattedValue}
          onChange={this.onChange.bind(this)}
        />
        {this.props.errorMessage && (
          <ErrorMessage
            message={this.props.errorMessage}
            hidden={this.props.delayError}
          />
        )}
      </div>
    );
  }
}

export default FormattedInput;
